
.vux-cell-form-preview {
	background-color: white;
}
#leasesDiv .weui-form-preview__bd {
	text-align: left;
	background-color: white;
}
#leaseDetailDiv .weui-cell:before {
	border: none
}
#leaseDetailDiv .vux-cell-primary {
	-webkit-box-flex: 0;
	-webkit-flex: none;
	        flex: none;
	min-width: 6em;
}
#leasesDiv .weui-cells {
	margin-top: 0;
}
#leasesDiv .weui-cells__title {
	margin-top: 5px;
}

/* #leasesDiv .vux-no-group-title {
	background: #4e79a8;
	color: #ffffff;
} */
#leasesDiv.weui-form-preview__hd:after {
	border-bottom: 1px solid #42B983;
}
#leasesDiv .weui-form-preview__hd .weui-form-preview__label {
	width: 100%;
	color: #ffffff;
	display: inline-block;
	float: none;
	text-align-last: left;
}
.weui-form-preview__value {
	color: #4e79a8;
}



