.container {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
}


.form {
  padding: 56px 0 0;
  display: -webkit-box;
  display: -webkit-flex;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
  -webkit-flex-direction: column;
  flex-direction: column;
  line-height: 1.4;
  min-height: 100%;
  box-sizing: border-box;
  background-color: #fff;
  background-color: var(--weui-BG-2);
}

.form__top {
  padding: 0 32px;
  color: rgba(0, 0, 0, 0.9);
  text-align: center;
}

.form__title {
  font-size: 22px;
  font-weight: 700;
  line-height: 1.36;
}

.form__desc {
  font-size: 17px;
  margin-top: 16px;
  color: #666666;
}

.form__control-area {
  padding-left: 16px;
  padding-right: 16px;
  -webkit-box-flex: 1;
  -webkit-flex: 1;
  flex: 1;
  margin: 48px 0;
}

.form__cell_top {
  padding: 16px;
  display: flex;
  align-items: center;
  border-top: 1px solid rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.form__cell_bottom {
  padding: 16px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid rgba(0, 0, 0, 0.1);
}

.cell__hd {
  padding-right: 16px;
}

.cell__label {
  max-width: 5em;
  margin-right: 8px;
  padding-right: 16px;
}

.form__opr_area {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0px 0px 96px;
}

input {
  width: 100%;
  border: 0;
  outline: 0;
  -webkit-appearance: none;
  background-color: transparent;
  font-size: inherit;
  color: inherit;
  height: 1.41176471em;
  line-height: 1.41176471;
}



.disabled {
  background-color: gray;
  opacity: 0.6;
  cursor: not-allowed;
}

.card {
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
  width: auto;
  padding: 5px 5px;
  margin: 10px 10px;
  border-radius: 5px;
}

.card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}

[v-cloak] {
  display: none;
}


