
#snapsDiv .vux-tab .vux-tab-item.vux-tab-selected {
	color: #4e79a8;
	border-bottom: 3px solid #4e79a8;
}
#snapsDiv .weui-cell:before {
	border: none
}
#snapsDiv .billTitle {
	font-size: 20px;
}
#snapsDiv .billTitle .weui-cell__ft {
	color: #EC8B89;
}
#snapsDiv .billTitle .vux-cell-bd {
	color: #4e79a8;
}
#snapsDiv .weui-form-preview__bd {
	text-align: left;
}
#snapDetailDiv .weui-form-preview__bd {
	text-align: right;
}
#snapDetailDiv .weui-cells {
	margin-top: 0px;
}
#snapsDiv .vux-tab-ink-bar {
	background-color: #4e79a8
}

/*#billHead{background: #4e79a8 ;}*/
#billHead .vux-label {
	color: #EC8B89;
	font-size: 20px;
}
#billHead .weui-cell__ft {
	color: #42B983;
}



